<template>
  <div>
    <el-button type="primary" @click="$router.push('category/add')">添加<i class="el-icon-circle-plus-outline"></i></el-button>
    <div class="table-bg">
      <el-table :data="list" style="width: 100%;margin-bottom: 20px;"
          row-key="id"
          default-expand-all
          :tree-props="{children: 'children'}">
        <el-table-column label="分类名称" prop="catename"></el-table-column>
        <el-table-column label="分类ID" prop="id"></el-table-column>
        <el-table-column label="分类图片">
           <template slot-scope="scope">
               <img v-if="scope.row.img" :src="scope.row.img | imgurl" style="width:80px;" alt=""/>
              <span v-else>暂无！</span>
            </template>
        </el-table-column>
        <el-table-column label="状态" prop="status">
          <template slot-scope="scope">
              <el-tag v-if="scope.row.status==1" type="success">正常</el-tag>
              <el-tag  v-else type="info">禁用</el-tag>
            </template>
        </el-table-column>
        <el-table-column label="操作" prop="status">
            <template slot-scope="scope">
              <el-button @click="$router.push('/category/edit/'+scope.row.id)"  icon="el-icon-edit"  type="success" circle size="small"></el-button>
              <el-button @click="del(scope.row.id)" type="danger" icon="el-icon-delete" circle size="small"></el-button>
            </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { mapGetters,mapActions } from "vuex"
export default {
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters({
      list:"category/catelist"
    })
  },
  mounted() {
    if(!this.list.length){
      this.getlist();
    }
  },
  methods: {
     ...mapActions({
       getlist:"category/getlist",
       del:"category/del"
     })
  },
  components: {}
};
</script>
<style scoped>
</style>


